import API from "../../Services/API";
import { DocToString } from "../../Services/Calcs";
import { ValidarIdentificacion, ValidateInput } from "../../Services/Validation";
import Preferences from "../../Services/Preferences";
import { OrderState } from "./index";

export function ValidateSave(state: OrderState) {
    var { numeroTransaccion, ciudad, sector, direccion,
        documento, identificacion, razonSocial, telefono_factura, direccion_factura,
        email_factura, loading, usarImagen, imgTransaccion
    } = state;
    if (loading) return { loading: true };

    var results: any = {};
    var choosen = state.productList.filter(x => x.cantidad);

    if (!choosen.length) return { productList: "Debes elegir por lo menos un producto" };

    if (isEmpty(ciudad)) results.ciudad = "Digita la ciudad";
    if (isEmpty(sector)) results.sector = "Digita el sector";
    if (isEmpty(direccion)) results.direccion = "Digita la direccion";

    if (isEmpty(documento)) results.documento = "Selecciona un documento de identificación";
    if (isEmpty(identificacion)) {
        results.identificacion = "Digita el número de " + DocToString(documento);
    } else {
        let error = ValidarIdentificacion(identificacion, documento)
        if (error) results.identificacion = error;
    }
    if (isEmpty(razonSocial)) results.razonSocial = "Digita la Razon Social de la factura";
    if (isEmpty(telefono_factura)) {
        results.telefono_factura = "Digita el numero de Telefono de facturación";
    } else {
        let error = ValidateInput.tel(telefono_factura);
        if (error) results.telefono_factura = error;
    }
    if (isEmpty(direccion_factura)) results.direccion_factura = "Digita la dirección de facturación";
    if (isEmpty(email_factura)) results.email_factura = "Digita el email de facturación";
    if (isEmpty(email_factura)) {
        results.email_factura = "Digita el email de facturación";
    } else {
        let error = ValidateInput.email(email_factura);
        if (error) results.email_factura = error;
    }

    if (usarImagen) {
        if (isEmpty(imgTransaccion)) results.imgTransaccion = "Agrega la imagen del comprobante de pago";
    } else {
        if (isEmpty(numeroTransaccion)) results.numeroTransaccion = "Digita el numero de comprobante de pago";
    }

    if (Object.keys(results).length) return results;
    return null;
}
export function Save(state: OrderState, imgFileTransaccion?) {
    const { ciudad, sector, direccion, observacion, referencia,
        documento, identificacion, razonSocial, telefono_factura, direccion_factura, email_factura,
        descuentoAplica, position, subtotal, usarImagen
    } = state;
    var choosen = state.productList.filter(x => x.cantidad);

    if (usarImagen) {
        var fileTransaccion = imgFileTransaccion;
    } else {
        var numeroTransaccion = state.numeroTransaccion;
    }
    return API.SaveOrder({
        ciudad, sector, direccion, observacion, referencia,
        documento, identificacion, razonSocial, telefono_factura, direccion_factura, email_factura,
        detalle: choosen,
        descuentoPorcentaje: descuentoAplica && descuentoAplica.porcentaje,
        descuentoTotal: descuentoAplica && descuentoAplica.valor,
        total: subtotal - ((descuentoAplica && descuentoAplica.valor) || 0),
        position, numeroTransaccion, fileTransaccion
    }).then(x => {
        Preferences.Set("current_order");
        Preferences.Set("direccion", {
            direccion, ciudad, sector, referencia
        });
        Preferences.Set("factura", {
            identificacion, documento, razonSocial,
            telefono_factura, direccion_factura, email_factura,
        });
        return {
            currentTab: 0,
            descuentoAplica: null,
            subtotal: 0,
            numeroTransaccion: "",
            observacion: "",
            position: null,
            loading: null,
            productList: state.productList.map(x => {
                x.cantidad = 0;
                return x;
            }),
            isDirty: false,
            id: x.id
        }
    })
}

export function GuardarBorrador(state: OrderState) {
    // actualizar todo!
    var choosen = state.productList.filter(x => x.cantidad).map(x => ({ id: x.id, cantidad: x.cantidad }));
    const { numeroTransaccion, observacion,
        direccion, ciudad, sector, referencia, position,
        documento, identificacion, razonSocial, telefono_factura, direccion_factura, email_factura
    } = state;
    Preferences.Set("current_order", {
        numeroTransaccion, direccion, ciudad, choosen,
        sector, observacion, referencia, position,
        documento, identificacion, razonSocial, telefono_factura, direccion_factura, email_factura
    });
}

function isEmpty(val) {
    return !(val && val.length && val.trim().length);
}