import React from "react";
import { TextField } from "@material-ui/core";
export default function Input(props: InputProps) {
    var { error, inputProps, ..._props } = props;
    var other_props: any = {};
    if (error) {
        other_props.error = true;
        other_props.helperText = error;
    }
    if (!other_props.inputProps) other_props.inputProps = {};
    other_props.inputProps.id = _props.name;
    return <TextField
        {...other_props}
        {..._props}
    >{props.children}
    </TextField>
}

interface InputProps {
    type?: string;
    value?: string;
    error?: string;


    [key: string]: any;
}