import React from "react";
import { Paper, Typography, TextField, Button, Divider, CircularProgress, CardActionArea, CardMedia, Switch, FormControlLabel } from "@material-ui/core";
import { Image } from "@material-ui/icons";
export default function (props: {
    onValueChange: Function,
    numeroTransaccion: string,
    onImgClick: any,
    usarImagen?: boolean,
    imgTransaccion: string,
    items: any[],
    onDone: any,
    total: number,
    loading: boolean,
    visible: boolean
}) {
    return (<Paper style={{ maxWidth: 400, padding: 10, marginTop: 10, height: "100%", marginLeft: "auto", marginRight: "auto", display: props.visible ? "block" : "none" }}>
        <form onSubmit={props.onDone} style={{ textAlign: "center" }}>
            <Typography style={{ marginTop: 10, marginBottom: 10, textAlign: "center" }} variant="h6">Total a Pagar: ${props.total.toFixed(2)}</Typography>
            <Divider />
            <div style={{ padding: 20 }}>
                <Typography>Realiza tu pago mediante Transferencia o Deposito Bancario a la cuenta:</Typography>
                <Typography>PRODUBANCO. Cta. Cte. #2005236823</Typography>
                <Typography>RUC. 1792627192001</Typography>
                <Typography>COSMETICOSNATU CIA.LTDA.</Typography>
            </div>
            <FormControlLabel
                label="Subir una Imagen"
                control={<Switch
                    checked={Boolean(props.usarImagen)}
                    onChange={e => props.onValueChange("usarImagen", e.currentTarget.checked)}
                />}
            />

            {!props.usarImagen && <TextField
                style={{ width: "calc(100% - 20px)", margin: 10 }}
                variant="outlined"
                inputMode="numeric"
                inputProps={{ id: "numeroTransaccion" }}
                onChange={event => props.onValueChange("numeroTransaccion", event.currentTarget.value)}
                value={props.numeroTransaccion}
                label="Numero de Comprobante *"
            />}
            {props.usarImagen &&
                <CardActionArea onClick={props.onImgClick} style={{ textAlign: "center" }}>
                    {props.imgTransaccion
                        ? <CardMedia className="transaction-img" title="Imagen" image={props.imgTransaccion} />
                        : <div className="transaction-img" style={{ display: "table", textAlign: "center", margin: "auto" }} >
                            <div style={{ display: "table-cell", verticalAlign: "middle" }}>
                                <Image />
                                <Typography>Agrega una imagen</Typography>
                            </div>
                        </div>}
                </CardActionArea>}

            <Button type="submit" disabled={props.loading} variant="outlined" color="primary" style={{ margin: 10, width: "calc(100% - 20px)" }}>
                {props.loading ? <CircularProgress size={25} /> : "ENVIAR"}
            </Button>
        </form>
    </Paper >)
}